import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import middleware from "./middleware";

import authentication from "@/router/modules/authentication";
import errors from "@/router/modules/errors";
import general from "@/router/modules/general";
import countries from "@/router/modules/countries";
import roles from "@/router/modules/roles";
import users from "@/router/modules/users";
import store from "@/store";
import banks from "@/router/modules/banks";
import transportTypes from "@/router/modules/transport-types";
import purposeOfUse from "@/router/modules/purpose-of-use";
import packingCategories from "@/router/modules/packing-categories";
import clients from "./modules/clients";
import variables from "./modules/variables";
import orders from "./modules/orders";
import goods from "@/router/modules/goods";
import employees from "@/router/modules/employees";
import workPositions from "@/router/modules/work-positions";
import services from "@/router/modules/services";
import guarantees from "@/router/modules/guarantees";
import payments from "@/router/modules/payments";
import invoices from "@/router/modules/invoices";
import branches from "@/router/modules/branches";
import cities from "@/router/modules/cities";
import subdivisions from "@/router/modules/subdivisions";
import emailTemplates from "@/router/modules/email-templates";
import statistics from "@/router/modules/statistics";
import documentTemplates from "@/router/modules/document-templates";
import expertiseTypes from "@/router/modules/expertise-types";
import profile from "@/router/modules/profile";
import goodsCategories from "@/router/modules/goods-categories";
import organizationForms from "@/router/modules/organization-forms";
import exchangeRates from "@/router/modules/exchange-rates";
import measureUnits from "@/router/modules/measure-units";
import aaijOrders from "@/router/modules/aaij-orders";
import notifications from "@/router/modules/notifications";

const routes: Array<RouteConfig> = [
  ...errors,
  ...general,
  ...authentication,
  ...roles,
  ...countries,
  ...users,
  ...banks,
  ...transportTypes,
  ...purposeOfUse,
  ...packingCategories,
  ...clients,
  ...variables,
  ...orders,
  ...goods,
  ...employees,
  ...workPositions,
  ...services,
  ...guarantees,
  ...payments,
  ...invoices,
  ...emailTemplates,
  ...statistics,
  ...documentTemplates,
  ...branches,
  ...cities,
  ...subdivisions,
  ...profile,
  ...goodsCategories,
  ...expertiseTypes,
  ...organizationForms,
  ...exchangeRates,
  ...measureUnits,
  ...aaijOrders,
  ...notifications
];

Vue.use(VueRouter);

store.dispatch("localization/setFromRouter");

const router = new VueRouter({
  mode: "history",
  base: store.getters["localization/getCurrent"],
  routes
});

middleware.apply(router);

export default router;
